       persistent
       max-width="600px"
   >
     <template v-slot:activator="{ on, attrs }">

       <v-btn class="ml-2 mt-5" outlined rounded small
              v-bind="attrs"
              v-on="on">
         {{label}}
       </v-btn>

     </template>
     <v-card>
       <v-card-title>
         <span class="headline">User Profile</span>
       </v-card-title>
       <v-card-text>
         <v-container>
           <v-row>
             <v-col
                 cols="12"
                 sm="12"
                 md="12"
             >
               <v-text-field
                   label="money"
                   required
                   :value="number"
                   @input="input"
               ></v-text-field>
             </v-col>
           </v-row>
         </v-container>
         <small>*indicates required field</small>
       </v-card-text>
       <v-card-actions>
         <v-spacer></v-spacer>
         <v-btn
             color="blue darken-1"
             text
             @click="dialogShow = false"
         >
