         </v-btn>
         <v-btn
             color="blue darken-1"
             text
             @click="confirm"
         >
           Confirm
         </v-btn>
       </v-card-actions>
     </v-card>
   </v-dialog>


</template>

<script>

  export default {
    props:{
      label:String,
      value:String,

    },
    data() {
      return {
        dialogShow:false,
        number: '',
      }
    },
    computed: {
    },
    methods: {
      confirm() {
        this.dialogShow = false;
        this.$emit('confirm')
      },
      input(event){
        this.$emit('input',event)
      }
    }
  }
</script>

