                <el-col :span="6" style="margin-top:10px;">
                  <img v-if="swapCoin=='PCFX'"src="cfx-logo.svg" style="width:40px;">
                  <img v-else src="cfx-white.svg" style="width:32px;">
                </el-col>
                <el-col v-if="swapCoin =='CFX'" :span="12"
