    </div>
    <el-row type="flex" justify="center" class="pool" style="margin-top:-270px;cursor:pointer;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <!--          <img src="second-home-bg.png" style="width:100%;">-->
        <div class="shadow-border" @click="gotoPoolPage">
          <el-row>
            <el-col :xs="24" :sm="24":lg="12">
              <div style="color:#ffffff;">

                <el-row type="flex" justify="center">
