                        </div>
                        <div
                                style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">

                          <div class="money-text"
                              style="margin-left:20px;text-align:left;font-weight: 800;display: inline-block;color: #FFFFFF;background: linear-gradient(77.7398deg, #C9225B 0.9521484375%, #FF3278 60.6689453125%, #FC9E7C 100%);-webkit-background-clip: text;-webkit-text-fill-color: transparent; letter-spacing:8px">
                            ${{ poolAwardBalance }}
                          </div>
                        </div>
                      </el-row>
                      <el-row>
                        <div style="margin-top:-10px;padding-bottom: 10px;">
                          This week’s prize value
                        </div>
                      </el-row>

                    </div>
                  </el-col>
                </el-row>

              </div>
            </el-col>
            <el-col :xs="24" :sm="24" :lg="12">
              <div style="margin-top:30px;">
                <el-row type="flex" justify="center" style="justify-content: center;align-items: center;">
                  <el-col :span="24">
                    <div style="width:100%;">
                    <circular-count-down-timer
                        :initial-value="prizePeriodRemainingSeconds"
                        :stroke-width="3"
                        :seconds-stroke-color="'#ffffff'"
                        :minutes-stroke-color="'#ffffff'"
                        :hours-stroke-color="'#ffffff'"
                        :days-stroke-color="'#ffffff'"
                        :underneath-stroke-color="'#E91E63'"
                        :seconds-fill-color="'rbga(#ffffff,0)'"
                        :minutes-fill-color="'rbga(#ffffff,0)'"
                        :hours-fill-color="'rbga(#ffffff,0)'"
                        :days-fill-color="'rbga(#ffffff,0)'"
                        :size="countdownTimerWidth"
                        :padding="countdownTimerPadding"
                        :hour-label="'hours'"
                        :minute-label="'minutes'"
                        :second-label="'seconds'"
                        :show-second="true"
                        :show-minute="true"
                        :show-hour="true"
                        :show-negatives="true"
                        :paused="false"
                        :notify-every="'minute'"
                        style="color:#ffffff;"
                    ></circular-count-down-timer>
                    </div>
                  </el-col>
                </el-row>
                <el-row style="margin-top: 30px;">
                  <el-col :offset="1" :span="10">
                    <vs-button block @click.stop="showDepositDialog"
                               style="font-size:20px;line-height:40px;background:linear-gradient(90deg, #E91E63, #FC9E7C);"
                               color="#E91E63" >
                      存款
                    </vs-button>
                  </el-col>
                  <el-col :span="10" :offset="2">
                    <vs-button @click.stop="captureAwardBalance" block border style="font-size:20px;line-height:40px;"
                               color="#E91E63">
                      提现
                    </vs-button>
                  </el-col>
                </el-row>
                <el-row style="color:white;margin-bottom: 30px;margin-top:10px;">

                  <el-col :offset="1" :span="1" style="">
                    <div style="display:inline-block;width: 20px;height: 20px;background: linear-gradient(78deg, #F1508E 0%, #F56988 35%, #F88382 81%, #FC9E7C 100%);
  border-radius: 50%;">
                    </div>
                  </el-col>

                  <el-col :span="2" style="margin-top:-4px;padding-left:10px;">
                    12.4%APR
                  </el-col>

                  <el-col :xs="{span:8,offset:11}" :sm="{span:4,offset:15}" style="text-align: right;margin-top:-4px">
                    查看奖池
                  </el-col>
                </el-row>
              </div>
            </el-col>

          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row style="margin:0 auto;margin-top:40px;color:#999999">
      您投入的资金越多，赢得的机会也越多。
    </el-row>

    <vs-dialog width="300px" not-center v-model="depositDialogShow">
      <template #header>
        <h4 class="not-margin">
          存 <b>PCFX</b> 赢得奖金
        </h4>
      </template>


      <div class="con-content">
        <vs-input v-model="depositPoolMoney" placeholder="请输入数量" type="number"></vs-input>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="confirmDeposit" transparent>
            Ok
          </vs-button>
          <vs-button @click="active3=false" dark transparent>
            Cancel
          </vs-button>
        </div>
      </template>
    </vs-dialog>
  </el-main>
</template>


<script>
import {VueGrid, VueCell} from "vue-grd";
import config from "@/config";
import {
  PrizePool,
  PCFX,
  TokenFaucet,
  Sponsor,
    AdminControl
} from "@/contracts";
import {BigNumber} from "bignumber.js";
import DialogButton from "@/componentsMine/DialogButton";
import {Conflux} from "js-conflux-sdk";


export default {
  name: "home-view",
  components: {
    "vue-grid": VueGrid,
    "vue-cell": VueCell,
    "dialog-button": DialogButton,
  },
  data() {
    return {
      dialog: false,
      pools: config.pools,
      loading: false,
      fixBoxStyleWidth: {
        width: "",
      },
      priceBSWAPUSDT: null,
      priceLFIBAI: null,
      rewardProcess: 78,
      apy: {
        1: "--",
        2: "--",
      },
      poolWithMultipleWinnersBuilder: {},
      compoundPrizePoolHarnessProxyFactory: {},
      compoundPrizePoolAddress: '',
      periodicPrizeStrategyAddress: '',
      ticketAddress: '',
      daiNumber: 0,
      poolBalance: 0,
      balanceOfCreditLastTime: 0,
      poolAwardBalance: 0,
      cToken: '',
      prizePeriodRemainingSeconds: 0,
      prizePeriodEndAt: 0,
      ticketNumber: 0,
      balanceOfPoolGo: 0,
      dripRatePerSecond: 0,
      exchangeRateMantissa: 0,
      poolGoUnClaimed: 0,
      reserveTotalSupply: 0,
      cfxStakeNumber: 0,
      model: 0,
      colors: [
        'primary',
        'secondary',
        'yellow darken-2',
        'red',
        'orange',
      ],
      depositDialogShow: false,
      cfxMoney: '',
      approveDepositShow: false,
      approveMoney: '',
      depositPoolDialogShow: false,
      depositPoolMoney: '',
      withDrawPoolDialogShow: false,
      withDrawPoolMoney: '',
      timelockMoney: '',
      withDrawPoolMoneyTimeLock: "",
      timelockTotalSupply: 0,
      swapMoney: '',
      translateMoney:0,
      windowHeight: 0,
      windowWidth: 0,
      choosePool:0,
      swapCoin:'CFX',

    };
  },
  computed: {
    pcfxContract:function(){
      return this.$store.state.web3.pcfxContract;
    },
    ticketContract:function(){
      return this.$store.state.web3.ticketContract;
    },
    poolGoContract:function(){
      return this.$store.state.web3.poolGoContract;
    },
    prizePoolContract:function(){
      return this.$store.state.web3.prizePoolContract;
    },
    periodicPrizeStrategyContract:function(){
      return this.$store.state.web3.periodicPrizeStrategyContract;
    },
    tokenFaucetContract:function(){
      return this.$store.state.web3.tokenFaucetContract;
    },
    cfxStakeContract:function(){
      return this.$store.state.web3.cfxStakeContract;
    },
    firstPoolColor:function(){
      if (this.choosePool ==0){
        return '#E91E63'
      }else
        return '#FFFFFF'
    },
    secondPoolColor:function(){
      if (this.choosePool ==1){
        return '#E91E63'
      }else
        return '#FFFFFF'
    },
    countdownTimerWidth: function () {
      console.log(this.windowWidth);
      if (this.windowWidth < 500) {
        return this.windowWidth/5
      } else if (this.windowWidth <= 1200) {
        return this.windowWidth/5
      } else {
        return this.windowWidth/12
      }
    },
    countdownTimerPadding: function () {
      if (this.windowWidth < 500) {
        return 3
      } else if (this.windowWidth <= 768) {
        return 8
      } else {
        return 10
      }
    }
  },
  created() {
    const that = this;
    that.windowHeight = parseInt(`${window.innerHeight}`);
    that.windowWidth = parseInt(`${window.innerWidth}`);
    window.onresize = () => {
      return (() => {
        that.windowHeight = parseInt(`${window.innerHeight}`);
        that.windowWidth = parseInt(`${window.innerWidth}`);
      })();
    }
  },

  async mounted() {
    setInterval(this.update, 5000)
  },
  methods: {
    async sponsor(){
      let that = this;
      let sponsor = new Sponsor();
      await sponsor.setSponsorForGas(that.$store.state.web3.account,100,config.tokenFaucetProxyFactoryAddress,31000);
      await sponsor.setSponsorForCollateral(that.$store.state.web3.account,100,config.tokenFaucetProxyFactoryAddress);

      await sponsor.setSponsorForGas(that.$store.state.web3.account,100,config.tokenFaucetAddress,31000);
      await sponsor.setSponsorForCollateral(that.$store.state.web3.account,100,config.tokenFaucetAddress);

      await sponsor.setSponsorForGas(that.$store.state.web3.account,100,config.pcfxAddress,31000);
      await sponsor.setSponsorForGas(that.$store.state.web3.account,100,config.prizePoolAddress,31000);

      await sponsor.setSponsorForCollateral(that.$store.state.web3.account,100,config.pcfxAddress);
      await sponsor.setSponsorForCollateral(that.$store.state.web3.account,100,config.prizePoolAddress);


    },
    async destroy(){
      let that = this;
      let adminControl = new AdminControl();
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acgbfs32ac0evxh46cswsd2zg73wewsr6atrx76c43");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acgmvvhzmefa2acgzfbmv3x6d1z3mnu4xa1scjx0fx");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acdhs0v8w5n32ee9kk44v6gyfme786zgxakt5fd7py");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acdpmjyw7kse193vefr21c7kfeycvh0uspwwy8jp0d");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acg3kamftcsfm75w3d207uvw37jk33fxd2ua80bp38");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acb22ewcy1wawktwc3usry7jzn50vw3j72c31x1bw4");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:ach4gkpjdmba4xxgw06m8br1687u1chfgaurgfyxu7");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acg3pr3bgym2xrumjvh2fc1cm30n269jz6kgc1x4dt");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:achngxdpjnk9puxm3sa50ddafwzrkw73w6jrhv99xd");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acbkggtr2t26sy15put2ynha1w8s2wgjz670drggrp");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acaus8bff59tdc1zpfgwy843y3yyu7p46a5g945z54");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acdgv21gr8ga2bd7332vu0snh843a69c22ysek2bm4");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acd4hu4czcffv30ngtn9s078yprs5t1dupgb9sz21h");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:achtdbczjh5u4czn74harmhudr35a8repyyemajjrd");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acgzk38ge1h071hwy7nazp93aetkujfp3ay27bhwfr");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acducwk03y33hhyg68rue1jfukwe215sxyv8t1yfrp");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acd4va60gvuchdsdu4vju2pfgatzfwrasy89hdtx6t");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:achkvfvs87xshtxycukb9f7j74hujnetx6eaedv6mm");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acbret3uhy0r90resgfgey5ksbxndc7py69nbr7e9j");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acbver3h4fay2vwamv9j1uv7gfy20udjk6cb2ecr0w");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acf9e9z9x75datpju2y692yux8gmcj2gcp8fdvnzgr");
      await adminControl.destroy(that.$store.state.web3.account,"cfx:acgbfs32ac0evxh46cswsd2zg73wewsr6atrx76c43");

    },
    async setAdmin(){
      let that = this;
      let adminControl = new AdminControl();
      await adminControl.setAdmin(that.$store.state.web3.account,config.prizePoolAddress,"net2999:aamp4fgakucjtzagtpu09p23d2kkscyumpnz37mrw9");
    },
    changeSwapMoney(){
        this.translateMoney = this.swapMoney;
    },
    async swap(){
      var that = this;
      if (that.swapCoin == "CFX") {
        let status = await that.pcfxContract.deposit(that.$store.state.web3.account, that.swapMoney);
      }else{
        let status = await that.pcfxContract.withdraw(that.$store.state.web3.account, that.swapMoney);
      }
    },

    exchange(){
      if (this.swapCoin == 'CFX'){
        this.swapCoin = 'PCFX'
        this.obtainCoin = 'CFX'
      }else{
        this.swapCoin = 'CFX'
        this.obtainCoin = 'PCFX'
      }
    },
    switchPools(order){
      this.choosePool = order;
    },
    gotoPoolPage() {
      this.$router.push({
        path: '/pool'
      })
    },
    async update() {
      var that = this;
      this.confluxReader = new Conflux({
        url: config.web3Provider,
        networkId:  config.networkId
      });
      let blockStatus = await this.confluxReader.getStatus();
      let curBlockNum = blockStatus.blockNumber;
      // console.log("cToken "+await that.prizePoolContract.cToken());
      let totalFunds = await that.cfxStakeContract.balanceOfUnderlyingView();
      console.log("totalFunds"+totalFunds)
      let tickets = await that.prizePoolContract.accountedBalance();
      console.log("tickets"+tickets)
      let award = totalFunds.minus(tickets);
      that.poolAwardBalance = award.toFormat(2);
      that.poolBalance = (await that.prizePoolContract.accountedBalance()).toFormat(2);
      that.timelockTotalSupply = await that.prizePoolContract.timelockTotalSupply();
      that.dripRatePerSecond = await that.tokenFaucetContract.dripRatePerSecond();
      that.exchangeRateMantissa = await that.tokenFaucetContract.exchangeRateMantissa();
